@charset "UTF-8";
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Light.woff2") format("woff2"), url("../fonts/Gilroy-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Regular.woff") format("woff"), url("../fonts/Gilroy-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Medium.woff2") format("woff2"), url("../fonts/Gilroy-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Bold.woff2") format("woff2"), url("../fonts/Gilroy-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
.wrapper {
  max-width: 1920px;
  margin: 0 auto;
  width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  margin: 0 auto;
  background-color: #E5E5E5;
  font-family: "Gilroy", sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
}

.overflow-hidden {
  overflow: hidden;
}

img {
  display: block;
}

p,
h1,
h2,
h3 {
  margin: 0;
  padding: 0;
}

ul,
li {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

a {
  color: #ffffff;
  text-decoration: none;
}

.title-h2 {
  font-size: 36px;
  text-align: center;
  line-height: 49px;
  font-weight: 700;
}

@media screen and (max-width: 480px) {
  .title-h2 {
    font-size: 24px;
  }
}
.sub-title {
  font-weight: 400;
  font-size: 22px;
  line-height: 140%;
  text-align: center;
}

.content-container {
  margin: 0 auto;
  max-width: 1110px;
}

@media screen and (max-width: 1280px) {
  .content-container {
    padding: 0 10px;
  }
}
@media screen and (max-width: 480px) {
  .content-container {
    padding: 0 25px;
  }
}
.header {
  background-image: -webkit-image-set(url("../img/main-bg.webp") 1x, url("../img/main-bg@2x.webp") 2x);
  background-position: center center;
  background-size: cover;
  height: 950px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.header__content-top {
  width: 100%;
}

.header__nav {
  margin-top: 25px;
}

.header__list {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  flex-wrap: wrap;
}

.header__list-item {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 10px;
}

.header__list-item:nth-child(4) {
  flex-grow: 0.4;
}

.header__list-item-link {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #ffffff;
}
.header__list-item-link span {
  margin-left: 15px;
}

.header__content-bottom {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.header__link-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 80px;
}

.header__link-title {
  margin-bottom: 50px;
}

.header__brand-icons {
  width: 100%;
  padding: 25px 0;
  border-top: 1px solid #ffffff;
}

.header__brand-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.header__brand-list-item {
  border-right: 1px solid #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: auto;
  min-width: 180px;
}
.header__brand-list-item img {
  width: 160px;
  height: 60px;
  object-fit: contain;
  object-position: center center;
}

.header__brand-list-item:last-child {
  border-right: none;
}
.header__brand-list-item:last-child img {
  margin-right: 0;
}

.header__brand-list-item:first-child img {
  margin-left: 0;
}

.header__navigation-mobile {
  display: none;
  position: relative;
  min-height: 85px;
  z-index: 2;
  transition: 0.3s;
  background-image: linear-gradient(90deg, #333, #333);
  background-size: 0%;
  background-repeat: no-repeat;
  padding-top: 25px;
  margin-top: -25px;
}
.header__navigation-mobile img {
  position: absolute;
  left: 0;
  top: calc(50% - 26px);
  z-index: 2;
  width: 216px;
}

.header__list-mobile {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  margin-top: 30px;
  font-size: 22px;
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 30px;
  background: #333;
  transform: translateX(-100%);
  transition: 0.3s;
  margin: 0;
  padding-top: 150px;
}
.header__list-mobile li {
  list-style-type: none;
}

.header__mobile-social {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.header__mobile-personal {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  border-top: 1px solid #ffffff;
  border-bottom: 1px solid #ffffff;
  padding: 25px 0;
}
.header__mobile-personal img {
  width: 50px;
  height: auto;
  margin-right: 30px;
}

.header__list-item_mobile {
  margin: 15px 0;
  display: flex;
}

.header__list-mobile.active {
  overflow: auto;
}

.header__navigation-mobile.active {
  background-size: 100%;
}

@media screen and (max-width: 1280px) {
  .header__brand-list {
    margin: 0 -12px;
  }
}
@media screen and (max-width: 991px) {
  .header__list-item:nth-child(4) {
    flex-grow: 0;
  }
  .header__list-item:nth-child(4) img {
    width: 100px;
  }
}
@media screen and (max-width: 768px) {
  .header__list-item:nth-child(4) {
    width: 100%;
    margin-top: 15px;
    margin-bottom: 15px;
  }
  .header__list-item:nth-child(4) img {
    width: 300px;
  }
  .header__list-item-link {
    font-size: 18px;
  }
}
@media screen and (max-width: 480px) {
  .header {
    height: 650px;
  }
  .header__brand-icons {
    display: none;
  }
  .header__list-item-link {
    font-size: 22px;
  }
  .header__list-item-link svg {
    fill: #ffffff;
    width: 36px;
    height: 36px;
  }
  .header__navigation-mobile {
    display: block;
  }
  .header__navigation-burger-menu {
    width: 50px;
    height: 50px;
  }
  .header__list {
    display: none;
  }
  .header__list-mobile.active {
    transform: translateX(0);
  }
}
.button-dark {
  text-transform: uppercase;
  letter-spacing: 0.1em;
  line-height: 21px;
  font-size: 18px;
  font-weight: 500;
  padding: 15px 20px;
  border: 1px solid #ffffff;
  background-color: #333;
  transition: 0.3s;
}

.button-dark:hover {
  text-shadow: 0 0 20px #D6D6D6;
}

.beauty {
  margin-top: 100px;
  margin-bottom: 90px;
}

.beauty__content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.beauty__title-block {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.beauty__sub-title {
  color: #333;
}

.beauty__title {
  color: #333;
  margin: 15px 0 80px 0;
}

.beauty__navigation-btn {
  width: 100%;
  margin-bottom: 50px;
}

.beauty__list-btn {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.beauty__btn-item {
  width: 170px;
}
.beauty__btn-item a {
  width: 165px;
  height: 55px;
  color: #333;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #D6D6D6;
  transition: 0.3s;
  background-color: #E5E5E5;
}
.beauty__btn-item a::before {
  content: "";
  position: absolute;
  width: calc(100% + 2px);
  height: calc(100% + 2px);
  z-index: -1;
  top: 5px;
  left: 5px;
  border: 1px solid #D6D6D6;
  transition: color 0.3s;
}
.beauty__btn-item a:hover::before,
.beauty__btn-item a.active::before {
  left: 0;
  border-color: transparent;
}
.beauty__btn-item a:hover,
.beauty__btn-item a.active {
  height: 60px;
  width: 100%;
  background-color: #333;
  color: #ffffff;
  border: 1px solid transparent;
}

.beauty__list-card {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.beauty__card-item {
  position: relative;
  background-image: linear-gradient(180deg, rgba(180, 180, 180, 0) 20.31%, #333333 100%);
  width: 350px;
  height: 280px;
  margin-bottom: 30px;
}
.beauty__card-item img {
  position: absolute;
  left: 0;
  right: 0;
  object-fit: contain;
  object-position: center center;
  z-index: -1;
}
.beauty__card-item p {
  font-size: 24px;
  line-height: 28px;
  font-weight: 500;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  position: absolute;
  width: 100%;
  text-align: center;
  bottom: 30px;
}

@media screen and (max-width: 1100px) {
  .beauty__list-btn {
    flex-wrap: wrap;
  }
  .beauty__btn-item {
    width: 25%;
    margin: 10px 20px;
  }
  .beauty__btn-item a {
    width: auto;
  }
}
.button-light {
  background-color: #ffffff;
  border: 1px solid #ffffff;
  padding: 15px 30px;
  color: #333;
  font-size: 18px;
  font-weight: 500;
  line-height: 21px;
  text-transform: uppercase;
  transition: 0.3s;
}

.button-light:hover {
  text-shadow: 0 0 20px #333;
}

.our-salons {
  background-image: -webkit-image-set(url(../img/second-bg.webp) 1x, url(../img/second-bg@2x.webp) 2x);
  background-position: center center;
  background-repeat: no-repeat;
  padding: 100px 0;
}

.our-salons__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.our-salons__title {
  margin: 15px 0 30px 0;
}

.our-salons__text {
  font-size: 18px;
  max-width: 730px;
  text-align: center;
  margin-bottom: 60px;
}

.useful-articles {
  margin: 120px 0;
}

.useful-articles__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.useful-articles__sub-title {
  color: #333;
}

.useful-articles__title {
  color: #333;
  margin: 15px 0 30px 0;
}

.useful-articles__card-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.useful-articles__card-item {
  width: 350px;
  height: 370px;
  position: relative;
  background-image: linear-gradient(180deg, rgba(180, 180, 180, 0) 20.31%, #333333 100%);
  margin-bottom: 60px;
}
.useful-articles__card-item img {
  position: absolute;
  top: 0;
  left: 0;
}
.useful-articles__card-item p {
  position: absolute;
  text-align: center;
  width: 100%;
  bottom: 30px;
  font-size: 22px;
  padding: 0 15px;
}

.social-media {
  margin-bottom: 150px;
}

.social-media__content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.social-media__sub-title {
  color: #333;
}

.social-media__title {
  color: #333;
  margin-top: 15px;
  margin-bottom: 60px;
}

.social-media__first-row {
  display: flex;
}

.social-media__video-box {
  position: relative;
  cursor: pointer;
}

.social-media__video-play {
  position: absolute;
  left: calc(50% - 35px);
  top: calc(50% - 35px);
}

.social-media__second-row {
  display: flex;
}

.mailing-list {
  background-image: -webkit-image-set(url(../img/second-bg.webp) 1x, url(../img/second-bg@2x.webp) 2x);
  background-position: center center;
  background-repeat: no-repeat;
  padding: 100px 0;
  color: #ffffff;
}

.mailing-list__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.mailing-list__title {
  margin: 15px 0 60px 0;
}

.mailing-list__form-box {
  display: flex;
}

.mailing-list__benefits {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around;
  margin-right: 60px;
}

.mailing-list__benefits-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.mailing-list__benefits-item img {
  margin-right: 20px;
}
.mailing-list__benefits-item p {
  font-size: 18px;
}

.mailing-list__form-item {
  display: flex;
  align-items: center;
  border: 1px solid #ffffff;
  padding: 15px;
}
.mailing-list__form-item form {
  padding: 20px;
  background-color: #E5E5E5;
}
.mailing-list__form-item form input {
  padding: 15px 20px;
  text-transform: uppercase;
  outline: 1px solid transparent;
  border: 1px solid #D6D6D6;
}
.mailing-list__form-item form input::placeholder {
  color: #333;
  opacity: 0.5;
}
.mailing-list__form-item form button {
  cursor: pointer;
  background-color: #333;
  border: 1px solid #E5E5E5;
  text-transform: uppercase;
  color: #ffffff;
  font-size: 14px;
  padding: 15px 35px;
}

.footer {
