  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 250px;
  width: 100%;
}

.footer__card-title {
  color: #333;
  font-weight: 500;
  letter-spacing: 0.1em;
  line-height: 140%;
  text-transform: uppercase;
  padding-bottom: 10px;
