  margin-bottom: 15px;
}

.footer__card-text {
  font-size: 14px;
  color: #333;
  margin-bottom: 20px;
}

.footer__card-img-box {
  display: flex;
  align-items: center;
}
.footer__card-img-box img {
  margin-right: 20px;
}
.footer__card-img-box a {
  font-size: 14px;
  color: #333;
}

.footer__card-link-phone {
  margin: 20px 0;
}

.footer__copyright-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  font-size: 14px;
  color: #333;
  padding: 20px 0;
  border-top: 1px solid #333;
}
.footer__copyright-box a {
  font-size: 14px;
  color: #333;
}

.footer__copyright-made {
  font-weight: 600;
  text-decoration: underline;
}

.menu-btn {
  width: 30px;
  height: 30px;
  position: absolute;
  right: 0;
  top: calc(50% - 15px);
  z-index: 2;
  overflow: hidden;
}

.menu-btn span {
  width: 30px;
  height: 4px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffffff;
  transition: 0.3s;
  border-radius: 2px;
}

.menu-btn span:nth-of-type(1) {
  width: 20px;
  left: calc(50% - 5px);
}

.menu-btn span:nth-of-type(2) {
  top: calc(50% - 10px);
}

.menu-btn span:nth-of-type(3) {
  top: calc(50% + 10px);
}

/* Меняем гамбургер иконку, когда меню открыто */
.menu-btn.active span:nth-of-type(1) {
  display: none;
}

.menu-btn.active span:nth-of-type(2) {
  top: 50%;
  transform: translate(-50%, 0%) rotate(45deg);
}

.menu-btn.active span:nth-of-type(3) {
  top: 50%;
  transform: translate(-50%, 0%) rotate(-45deg);
}